@import '../../../styles/variables.scss';

.textarea {
	width: 100%;

	font-family: $main-font;

	border: 0;
	padding: .75rem 1rem;
	background: $secondary-bg-color;
	resize: none;
	border: 1px solid transparent;

	transition: border .2s ease;

	&:focus {
		border: 1px solid black;

	}
}
